body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.checked {
  -webkit-text-decoration: line-through black;
          text-decoration: line-through black; 
}

input{
  font-size:2.5em;
  background:rgb(249, 249, 130);
  padding: 10px;
  border: none;
  margin: 20px;
  border-radius: 20px;
  outline: none;
}
.button{
  font-size: 30px;
  font-family:'Love Ya Like A Sister', cursive;
  color: black;
  border:none;
  background: transparent;
}
.button2{
  font-size: 30px;
  font-family:'Love Ya Like A Sister', cursive;
  color: black;
  border-radius: 30px;

  background: transparent;
}
label{
  font-size: 30px;
  
}

body {
  background: content-box -webkit-linear-gradient(left,white,rgb(249, 249, 130));
  background: content-box linear-gradient(to right,white,rgb(249, 249, 130));
  font-family:'Love Ya Like A Sister', cursive;
}

ul{
  list-style: none;
  text-align:center;
  /* align-items: center; */
  font-size: 50px;
  /* margin-left: 150px; */
  /* margin-right:1000px ; */
}
li{
  border: 3px solid black;
  background: rgb(249, 249, 130);
  margin: 5px;
}

.Box {
  text-align: center;
  margin: 100px;
  margin-bottom: 0px;
  background-color:white;
  border: 5px solid black;
  border-radius: 50px;
  opacity: 0.6;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

